//get query string
var q = location.href.split('?')[1] || '';
var qparams = {};
q = q.split('&');

for (var i = 0; i <  q.length; i++){
    var pair = q[i].split('=');
    qparams[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1]);
}

module.exports = qparams;



